import { Box } from "@mui/material";
import { useMarketPageData } from "../../data/market.context";
import { Script } from "./Script";

export const Scripts = () => {
    const data = useMarketPageData()
    return <Box>
        {data.scriptsInfo.scripts.map((script)=>{
            return <Script key={`${script.url}-${script.content}-${script.name}-${script.version}-${script.ns}`} script={script}/>
        })}
    </Box>
}