import { IScriptData } from "@market/lib"
import { Box } from "@mui/material"
import { marketHtmlUtils } from "@src/markets/market-html-utils"
import { IFrameReactContainer } from "@fanfanlo"
interface IScriptProps {
    script: IScriptData
}


export const Script = (props: IScriptProps) => {
    return <Box>
        <Box>{props.script.name}</Box>
        <Box><span>
            {props.script.summary}
        </span></Box>
        
        <Box>
            <IFrameReactContainer url={props.script.url} urlAsContent={true} htmlBuilder={marketHtmlUtils.createHtml} />
        </Box>
    </Box>
}