import { IStoreWithData, storeUtils } from "@fanfanlo";
import { marketNs } from "./ns";
import { IScriptData } from "@market/lib";

// export interface IScriptData{
//     ns:string;
//     name:string;
//     version:string;
//     url?:string;
//     content?:string;
// }

export interface IScriptsInfo{
    scripts:IScriptData[];
    total:number;
    loadIsEnded:boolean;
    loadIsFailed:boolean;
}
export interface IMarketPageData{
    scriptsInfo:IScriptsInfo;
}

function createDefaultMarketPageData():IMarketPageData{
    return {
        scriptsInfo:{
            scripts:[
                {
                    ns:"@fanfanlo/scripts/emergency-alarm",
                    name:"emergency-alarm",
                    version:"1.0.0",
                    url:"/scripts/fanfanlo/scripts/emergency-alarm/index.js",
                    content:""
                }
            ],
            total:0,
            loadIsEnded:false,
            loadIsFailed:false
        }
    }
}
export function createMarketPageData():IMarketPageData{
    return createDefaultMarketPageData();
}

let loader:Promise<IStoreWithData<IMarketPageData>>;
export async function loadMarketPageData():Promise<IStoreWithData<IMarketPageData>>{
    if(loader)return loader;
    const defData = createDefaultMarketPageData();
    loader = storeUtils.getNamespaceStoreWithData<IMarketPageData>(marketNs.androidData, {defData, forceReset:false, mergeDefData:true});
    return loader;
}